<template>
  <div class="text-block" :class="{ 'text-block--editing': editing }">
    <div v-if="!editing" class="text-block__view" @click="startEdit">
      <p class="text-block__content">{{ block.textContent || 'Нажмите для редактирования...' }}</p>
      <button class="text-block__delete" @click.stop="$emit('delete')">
        <v-icon icon="mdi-delete-outline" size="18" />
      </button>
    </div>
    <div v-else class="text-block__edit">
      <textarea
        ref="textarea"
        v-model="draft"
        class="text-block__textarea"
        placeholder="Введите текст..."
        rows="3"
        @blur="save"
        @keydown.escape="cancel"
      />
    </div>
  </div>
</template>

<script>
import { BlocksCall } from '@/api/call'

export default {
  name: 'TextBlock',
  props: {
    block: {
      type: Object,
      required: true,
    },
    pageId: {
      type: String,
      required: true,
    },
  },
  emits: ['update', 'delete'],
  data() {
    return {
      editing: false,
      draft: '',
    }
  },
  methods: {
    startEdit() {
      this.draft = this.block.textContent || ''
      this.editing = true
      this.$nextTick(() => {
        this.$refs.textarea?.focus()
      })
    },
    async save() {
      if (this.draft === this.block.textContent) {
        this.editing = false
        return
      }

      try {
        await BlocksCall.updateBlock(this.pageId, this.block.id, {
          type: 'TEXT',
          textContent: this.draft,
        })
        this.$emit('update', { ...this.block, textContent: this.draft })
      } catch (error) {
        console.error('Failed to update block:', error)
      }
      this.editing = false
    },
    cancel() {
      this.editing = false
    },
  },
}
</script>

